.heading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 0 0 50px 0;

  .screen-heading {
    font-size: 32px;
    color: var(--header-foreground);
    font-family: "Poppins Bold";
  }

  .screen-sub-heading {
    letter-spacing: 3px;
    margin: 8px 0 10px 0;
    font-size: 12px;
    color: var(--blackOrWhite);
  }

  .heading-seperator {
    display: flex;
    align-items: center;
    position: relative;
    width: 180px;
    margin: 10px 0 0 0;

    .seperator-line {
      width: 100%;
      height: 2px;
      background-color: var(--blackOrWhite);
    }
    .seperator-blob {
      height: 10px;
      width: 100%;
      position: absolute;
      display: flex;
      justify-content: center;
    }
    .seperator-blob div {
      width: 35px;
      border-radius: 10px;
      background-color: var(--primary-light-color);
    }
  }
}
